.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;

}

.gpt3__navbar-links_logo img {
  width: 62.56px;
  height: 16.06px;
}

.gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container  p,
.gpt3__navbar-sign p,  
.gpt3__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    text-transform: capitalize;
    line-height: 25px;

    margin: 0 1rem;
    cursor: pointer;
}

.gpt3__navbar-links_container  p :hover, 
.gpt3__navbar-menu_container p :hover {
    text-decoration: underline #fff 1.5px;
}

.gpt3__navbar-sign button,
.gpt3__navbar-links_container button,
.gpt3__navbar-menu_container-links-sign button {
    padding: 0.5rem 1rem;
    display: flex;
    line-height: 25px;
    background: #ff4820;
    font-weight: 500;
    border-radius: 5px;
    color: #fff;
    font-family: var(--font-family);
    border: 0;
    outline: none;
    cursor: pointer;
}

.gpt3__navbar-menu {
    margin-left: 1rem;
    position: relative;
}

.gpt3__navbar-menu svg {
    cursor: pointer;
}

.gpt3__navbar-menu_container {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    top: 40px;
    right: 0;
    padding: 2rem;
    position: absolute;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 2);
}

.gpt3__navbar-menu_container p {
    margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
    display: none;
} 

@media  screen and (min-width: 1050px) {
   .gpt3__navbar-menu {
    display: none;
   }
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display:none;
    }
    .gpt3__navbar-menu {
        display: flex;
    }
    
} 

@media screen and (max-width: 700px) {
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
    
} 

@media screen and (max-width: 550px) {
    .gpt3__navbar{
        padding: 2rem;
    }

    .gpt3__navbar-sign {
        display: none;
    }
    
    .gpt3__navbar-menu_container {
        top: 30px;
    }
    
    .gpt3__navbar-menu_container-links-sign {
        display:block
    } 
    
    .gpt3__navbar-menu_container-links-sign button {
    margin: 0 0 0 0px;
}
} 